.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.signInButton {
  padding: 14px 20px;
  background-color: cornflowerblue;
  border: none;
  outline: none;
  color: white;
  border-radius: 50px;
  position: absolute;
  transform: translateX(-50%);
  bottom: 0;
  margin-bottom: 100px;
  font-weight: 500;
}

.welcomeText {
  text-align: left;
  padding-left: 40px;
  font-size: 32px;
}

.welcomePc {
  font-family: "Quicksand", sans-serif;
  color: cornflowerblue;
  margin-top: -32px;
  font-size: 40px;
}

header {
  background-color: white;
  text-align: left;
  font-weight: 500;
  font-family: "Quicksand", sans-serif;
  font-weight: 500;
  color: cornflowerblue;
  padding: 10px;
  position: fixed;
  width: 100%;
  box-shadow: 0px 0px 12px 0px rgba(161, 161, 161, 0.6);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.settingsProfileButton {
  border: none;
  outline: none;
  width: 32px;
  height: 32px;
  margin-right: 16px;
  background-color: transparent;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}

.settingsProfile {
  height: 32px;
  width: 32px;
  border-radius: 50px;
}

.isOnline {
  height: 9px;
  width: 9px;
  background-color: rgb(61, 211, 15);
  border-radius: 50px;
  border: 2.5px solid white;
  position: absolute;
}

.userName {
  font-size: 14px;
  color: #707070;
  font-weight: 600;
  padding: 10px 32px 10px 20px;
}

.buttonDescription {
  font-size: 10px;
  font-weight: 800;
  color: #707070;
  opacity: 0.6;
  padding-left: 20px;
}

.signOutButton {
  color: white;
  background-color: red;
  border: none;
  outline: none;
  float: left;
  font-size: 16px;
  border-radius: 0 0 20px 20px;
  padding: 8px 12px;
  width: 100%;
  margin-top: 5px;
}

.signOutButton:hover {
  background-color: tomato;
}

.logoutIcon {
  width: 20px;
  margin-top: 5px;
  float: left;
}

main {
  padding: 60px 0 60px 0;
}

.message {
  display: flex;
  align-items: center;
  color: #121212;
  font-weight: 400;
}

.profilePhoto {
  height: 40px;
  border-radius: 50px;
  margin: 0 10px;
}

.sent {
  flex-direction: row-reverse;
}

.sent .profilePhoto {
  display: none;
}

.sent p {
  padding: 8px 10px;
  border-radius: 50px;
  background-color: cornflowerblue;
  color: white;
  margin: 2px 10px 2px 0;
}

.received p {
  padding: 8px 10px;
  border-radius: 50px;
  background-color: rgb(226, 226, 226);
  margin: 6px 0;
}

form {
  width: calc(100% - 20px);
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  padding: 5px 10px;
  align-items: center;
  background-color: white;
  box-shadow: 0px 0px 20px -6px rgba(161, 161, 161, 0.6);
}

input {
  height: 40px;
  width: calc(100% - 10px);
  background-color: whitesmoke;
  border: none;
  outline: none;
  border-radius: 50px;
  margin-right: 10px;
  padding: 0 20px;
}

.sendButton {
  height: 40px;
  width: 40px;
  background-color: transparent;
  border: none;
  outline: none;
  border-radius: 50px;
}
